@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./assets/fonts/Brandon_Grotesque_light.otf') format('opentype');
	font-weight: 100;
	font-style: normal;
}
@font-face {
	font-family: 'Brandon Grotesque';
	src: url('./assets/fonts/Brandon_Grotesque_regular.otf') format('opentype');
	font-weight: normal;
	font-style: normal;
}

@font-face {
	font-family: 'Didot';
	src: url('./assets/fonts/DidotLTPro-Roman.ttf') format('truetype');
	font-weight: normal;
	font-style: normal;
}
@font-face {
	font-family: 'Didot';
	src: url('./assets/fonts/DidotLTPro-Italic.ttf') format('truetype');
	font-weight: normal;
	font-style: italic;
}

// 1. Resetting default browser styles
*,
*::before,
*::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

html {
	font-size: 62.5%;
	scroll-behavior: smooth;
}

// 2. Setting up typography
body {
	font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
	line-height: 1.5;
	font-size: 1.6rem;
	color: #333;
	background-color: #fff;
}

// 3. Defining a color scheme
:root {
	--primary-color: #007bff;
	--text-color: #929191;
	--background-color: #fff;
}

// 4. Creating a container class
.container {
	max-width: 1000px;
	margin: 0 auto;
	padding: 0 10px;
}

.text-center {
	text-align: center;
}

.mx-auto {
	margin: 0 auto;
}

//list styles
ul {
	list-style-type: none;
}

// link styles
a {
	text-decoration: none;
	color: rgb(200, 200, 200);
	&:hover,
	&:active {
		color: var(--text-color);
		text-decoration: underline;
	}
}

p,
.p {
	font-size: 1.8rem;
	font-family: 'Brandon Grotesque', sans-serif;
}

h1,
.h1 {
	font-size: 4rem;
	font-weight: light;
}
h2,
h2 {
	font-size: 2.5rem;
	font-weight: 100;
	font-family: 'Brandon Grotesque', sans-serif;
	text-transform: uppercase;
	margin-bottom: 5rem;
}

section {
	scroll-margin-top: 100px;
}

// ---------------------------------------- //

header {
	max-height: 8rem;
	background-color: #242323;
	position: sticky;
	top: 0;
	z-index: 3;

	nav {
		height: 8rem;
		position: relative;

		.nav-links {
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
			padding: 2rem 1rem;
			gap: 2rem;

			.burger {
				display: none;
			}

			li {
				text-align: center;
				padding: 0 1.5rem;

				a {
					font-size: 1.4rem;
					font-family: 'Brandon Grotesque', sans-serif;
					padding: 2rem 1.5rem;
					text-transform: uppercase;
				}
			}
		}
		.custom-dropdown {
			position: absolute;
			left: 3rem;
			top: 20%;

			cursor: pointer;
			.options-container {
				opacity: 0;
				transition: opacity 0.5s ease-in-out;
				display: flex;
				flex-direction: column;
				gap: 1rem;
				&.slideInDown {
					animation-name: slideInDown;
					animation-duration: 1s;
					animation-fill-mode: both;
				}
				.dropdown-option {
					text-decoration: none;
					&:hover {
						transform-origin: top center;
						animation-name: swing;
						animation-duration: 1s;
						animation-fill-mode: both;
					}
				}
			}
		}

		@keyframes slideInDown {
			0% {
				transform: translateY(-50%);
				opacity: 0;
			}
			100% {
				transform: translateY(0);
				opacity: 1;
			}
		}
		@keyframes swing {
			20% {
				transform: rotate3d(0, 0, 1, 15deg);
			}
			40% {
				transform: rotate3d(0, 0, 1, -10deg);
			}
			60% {
				transform: rotate3d(0, 0, 1, 5deg);
			}
			80% {
				transform: rotate3d(0, 0, 1, -5deg);
			}
			100% {
				transform: rotate3d(0, 0, 1, 0deg);
			}
		}


		@media screen and (max-width: 1023px) {
			.nav-links {
				position: absolute;
				height: 100vh;
				width: 100%;
				list-style: none;
				background-color: #333;
				flex-direction: column;
				align-items: flex-end;
				-webkit-clip-path: circle(100px at 90% -20%);
				clip-path: circle(100px at 90% -20%);
				transition: all 0.5s ease-out;
				pointer-events: none;
				transform: translateY(-100%);
				opacity: 0;

				li {
					opacity: 0;
					margin-bottom: 1rem;

					a {
						font-size: 3rem;
					}
				}
			}

			.burger {
				display: block;
				position: absolute;
				top: 50%;
				right: 20px;
				padding: 11px;
				border: 3px solid white;
				transform: translateY(-50%);
				cursor: pointer;

				& > div {
					width: 25px;
					height: 3px;
					background-color: white;
					margin: 5px;
					transition: all 0.5s ease-in-out;
				}
			}

			.nav-active {
				-webkit-clip-path: circle(1000px at 90% -10%);
				clip-path: circle(1000px at 90% -10%);
				transform: translateY(0);
				opacity: 1;
				pointer-events: all;

				& > li {
					opacity: 1;
					transition: opacity 0.5s ease-in-out;
					transition-delay: 0.5s;
					color: white;
				}
			}
		}
	}
}

main {
	.parallax-window {
		max-width: 980px;
		width: 100%;
		display: flex;
		justify-content: center;
		align-items: center;
		&:before {
			content: '';
			display: block;
			padding-top: 65.3061224%; // Calculated for aspect ratio (638/980 * 100)
		}

		@media (min-width: 1024px) {
			height: 638px;
			padding: 50px;
			&:before {
				display: none;
			}
		}
	}

	.social {
		position: fixed;
		right: 0;
		top: 50%;
		transform: translateY(-50%);
		display: flex;
		flex-direction: column;
		gap: 2rem;
		margin-right: 3rem;

		@media screen and (max-width: 1024px) {
			display: none;
		}
	}

	.social-mobile {
		display: none;

		@media screen and (max-width: 1024px) {
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 2rem;
			margin-top: 2rem;
		}
	}

	.hero {
		text-align: center;
		&__content {
			padding: 8rem 0;
			&__title {
				color: #242323;
				font-family: 'Brandon Grotesque', sans-serif;
				font-weight: 100;
			}
			&__subtitle {
				span:first-child {
					position: relative;
					&::before {
						content: '';
						position: absolute;
						left: -15px;
						top: 50%;
						transform: translate(-50%, -50%);
						border-top: 0.3px solid #242323;
						width: 20px;
						height: 1px;
					}
					&::after {
						content: '';
						position: absolute;
						right: -33px;
						top: 75%;
						transform: translate(-50%, -50%);
						border-top: 0.3px solid #242323;
						width: 20px;
						height: 1rem;
					}
				}
				span {
					font-family: 'Didot', serif;
					font-style: italic;
					font-size: 2rem;
				}
			}
		}
	}

	.about {
		margin: 0 auto;
		padding-top: 8rem;
		@media screen and (max-width: 1024px) {
			padding-top: 2rem;
		}
		&__content {
			margin-bottom: 2rem;
			&__text {
				font-family: 'Brandon Grotesque', sans-serif;
				font-style: italic;
				font-weight: 100;
				max-width: 580px;
			}
			&__card {
				max-width: 390px;
				max-height: 369px;
				padding: 100px 50px;
				text-align: center;
				background-color: white;

				@media screen and (max-width: 1024px) {
					padding: 20px;
					box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
					width: 80%; // Adjust the width as needed
					max-width: 600px; // Adjust max-width as needed
					margin: auto;
				}

				h2 {
					@media screen and (max-width: 1024px) {
						font-size: 2rem;
						margin-bottom: 10px;
					}
				}

				p {
					font-size: 1.8rem;
					@media screen and (max-width: 1024px) {
						font-size: 1.1rem;
						line-height: 1.5;
					}
				}
			}
		}
	}

	.amenities {
		padding-top: 7rem;
		&__cards {
			display: flex;
			flex-wrap: wrap;
			align-items: baseline;
			justify-content: center;
			gap: 3rem;
		}
		&__card {
			max-width: 280px;
			min-height: 200px;
			margin-bottom: 100px;

			@media screen and (max-width: 1024px) {
				margin-bottom: 2rem;
			}

			&__image {
				height: 50px;
				margin-bottom: 1rem;
			}
			p {
				margin-bottom: 3rem;
			}
			span {
				font-size: 1.8rem;
				font-family: 'Brandon Grotesque', sans-serif;
				font-style: italic;
				line-height: 27px;
			}
		}
		.transition-on-scroll > p {
			min-height: 58px;
		}
	}

	.gallery {
		&__cards {
			a {
				text-decoration: none;
			}
		}
		&__card {
			max-width: 350px;
			max-height: 630px;
			width: 100%;
			height: calc(100vw * (630 / 354));
			padding: 30px 10px 50px 10px;
			margin: 0 1rem;
			cursor: pointer;
			@media screen and (max-width: 1024px) {
				max-width: 150px;
				width: auto;
				height: calc(45vw * (630 / 354));
			}
			&:hover {
				p,
				span {
					position: relative;
					z-index: 2;
					opacity: 1;
				}
			}

			p {
				color: white;
				opacity: 0;
				transition: opacity 0.3s;
				&:hover,
				&:active {
					text-decoration: none;
				}
			}
			span {
				font-weight: 100;
				font-style: italic;
				font-family: 'Brandon Grotesque', sans-serif;
				font-size: 1.8rem;
				color: white;
				opacity: 0;
				transition: opacity 0.3s;
				&:hover,
				&:active {
					text-decoration: none;
				}
			}
		}
	}

	.reviews {
		margin: 0 auto;
		padding-top: 8rem;
		&__title {
			margin-bottom: 2rem;
		}
		img {
			margin-bottom: 2rem;
		}
		&__card {
			max-width: 390px;
			max-height: 369px;
			padding: 50px;
			text-align: center;
			background-color: white;
			box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);

			@media screen and (max-width: 1024px) {
				width: 80%;
				padding: 20px;
				margin: auto;
			}

			@media screen and (max-width: 1024px) {
				h2 {
					font-size: 2rem;
					margin-bottom: 10px;
				}

				p {
					font-size: 1.1rem;
					line-height: 1.5;
				}
			}
		}
		&__text {
			font-family: 'Brandon Grotesque', sans-serif;
			font-style: italic;
			font-weight: 100;
			max-width: 580px;
		}
	}

	.contacts {
		margin-top: 3rem;
		padding: 100px 0 20px 0;
		background-color: #c8c8c8;
		&__details {
			display: flex;
			flex-wrap: wrap;
			align-items: center;
			justify-content: space-between;

			@media screen and (max-width: 1024px) {
				flex-direction: column;
				align-items: center;
				justify-content: center;
				gap: 3rem;

				.slash {
					display: none;
				}
			}

			a,
			span {
				color: black;
				font-weight: 100;
				font-family: 'Brandon Grotesque', sans-serif;
				font-size: 1.8rem;
			}
		}
	}

	.location {
		position: relative;
		width: 100%;
		padding-bottom: 56.25%; /* 16:9 aspect ratio */
		height: 0;
		overflow: hidden;
		max-height: 630px;

		@media (min-width: 1120px) {
			padding-bottom: 0;
			height: 630px;
		}

		iframe {
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			max-height: 630px;
			border: 0;

			@media (min-width: 1120px) {
				height: 630px;
			}
		}
	}
}

